.bileti__data {
  padding-top: 44px;
  padding-bottom: 44px;
  background: #102120;
}

.bileti__data.schedule .schedule-row .match {
  max-width: initial;
}

.bileti__data.schedule .schedule-row .info {
  flex: none;
  margin-right: 0;
}

.bileti__data__title {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 24px;
  line-height: 110%;
  color: #FFFFFF;
  font-family: var(--font);
  margin-bottom: 40px;
}

.bileti__data__list {
  margin-bottom: 70px;
}
.bileti__data__list .schedule-section-list {
  margin-bottom: 10px;
}

.btn.btn-transparent-back {
  background-image: url("../img/bileti/btn-back.svg");
  background-size: 100% 100%;
}

.btn.btn-transparent-back:focus {
  color: #fff;
}

.btn.btn-transparent-back:after {
  display: none;
}

.btn.btn-transparent-back:before {
  display: none;
}

@media (max-width: 767px) {
  .bileti__data__title {
    margin-bottom: 30px;
  }
}
@media (max-width: 374px) {
  .btn.btn-transparent-back {
    font-size: 14px;
  }

  .match-detail-top-info .match-detail-buttons .btn {
    font-size: 14px;
  }
}
.bileti-sector-sec {
  position: relative;
  background: #f1f1f178;
  margin-bottom: -180px;
  padding-bottom: 110px;
}

.bileti-sector-sec .container-fluid {
  position: relative;
  z-index: 2;
}

.bileti-sector-sec__img {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 1;
}

.bileti-sector {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  padding-top: 162px;
  padding-bottom: 110px;
}

.bileti-sector__col {
  width: 33.3333333333%;
  padding: 0 15px;
  margin-bottom: 20px;
}

.bileti-sector__item {
  width: 100%;
}

.bileti-sector__img {
  position: relative;
  padding-bottom: 66%;
  background-size: cover;
  background-position: center;
  margin-bottom: 50px;
  display: block;
  box-shadow: 0px 60px 60px -10px rgba(0, 0, 0, 0.4);
}

.bileti-sector__img:before {
  content: "";
  display: block;
  background: linear-gradient(180deg, rgba(46, 48, 55, 0) 24%, rgba(46, 48, 55, 0.5) 54.87%, rgba(46, 48, 55, 0.85) 89.71%);
  box-shadow: 0px 60px 60px -10px rgba(0, 0, 0, 0.4);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  transition: 0.3s all;
}

.bileti-sector__img:hover:before {
  background: linear-gradient(180deg, rgba(46, 48, 55, 0.1) 24%, rgba(46, 48, 55, 0.6) 54.87%, rgba(46, 48, 55, 0.95) 89.71%);
}

.bileti-sector__text {
  position: absolute;
  left: 20px;
  bottom: 30px;
  right: 20px;
  font-weight: bold;
  font-size: 32px;
  line-height: 110%;
  color: #FFFFFF;
  font-family: var(--font);
  z-index: 2;
}

.bileti-sector__link {
  font-weight: bold;
  font-size: 18px;
  line-height: 110%;
  letter-spacing: 0.2em;
  color: #3E414A;
  font-family: var(--font);
  padding-bottom: 20px;
  position: relative;
  margin-left: 20px;
  display: block;
}

.bileti-sector__link:before {
  content: "";
  display: block;
  bottom: 0;
  position: absolute;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #E33046;
  transition: 0.3s all;
}

.bileti-sector__link:hover:before {
  width: 30px;
}

.bileti-sposobi__wrap {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.bileti-sposobi__col {
  width: 50%;
  padding: 0 15px;
  margin-bottom: 20px;
}

.bileti-sposobi__item {
  background: #FFFFFF;
  box-shadow: 0px 14px 24px -10px rgba(47, 51, 82, 0.1);
  padding: 20px 20px 40px;
  display: block;
  transition: 0.3s all;
}

.bileti-sposobi__item:hover {
  box-shadow: 0px 14px 24px -10px rgba(47, 51, 82, 0.3);
}

.bileti-sposobi__title {
  font-weight: bold;
  font-size: 24px;
  line-height: 110%;
  color: #348D8B;
  font-family: var(--font);
  margin-bottom: 15px;
}

.bileti-sposobi__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #3E414A;
  max-width: 370px;
}

.bileti-sposobi .page-section-header {
  margin-bottom: 30px;
}
.bileti-sposobi .page-section-header h2 {
  margin-bottom: 0;
}

@media (max-width: 991px) {
  .bileti-sector__col {
    width: 50%;
    margin-bottom: 50px;
  }

  .bileti-sector__img {
    margin-bottom: 30px;
  }

  .bileti-sector {
    padding-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .bileti-sector-sec {
    background-color: #fff;
  }

  .bileti-sposobi__col {
    width: 100%;
  }

  .bileti-sector {
    padding-top: 100px;
  }

  .bileti-sector__img {
    box-shadow: 0px 60px 60px -10px rgba(0, 0, 0, 0.1);
  }
}
@media (max-width: 575px) {
  .bileti-sector__col {
    width: 100%;
  }

  .bileti-sector-sec {
    padding-bottom: 50px;
  }
}